.register {
    form {
        border: none;
        display: flex;
        flex-direction: column;
        margin: 10px;
        max-width: 300px;
        border: 1px solid black;
        padding: 5px;
        text-align: center;
        border-radius: 10px;

        input {
            margin: 4px;

            &:last-child {
                background-color: dodgerblue;
                border: none;
                border-radius: 5px;
                color: white;
                padding: 2px;
                cursor: pointer;

                &[disabled] {
                    cursor: progress;
                    background-color: #aaaa;
                }

                &:focus {
                    outline: none;
                }
            }
        }
    }
}